                                               <th style=\"padding: 5px;\">Percentage</th>\r\n                                                        <th> </th>\r\n                                                        <th> </th>\r\n                                                        <th style=\"padding: 5px;\">1</th>\r\n                                                        <th style=\"padding: 5px;\">2</th>\r\n                                                        <th style=\"padding: 5px;\">3</th>\r\n                                                        <th style=\"padding: 5px;\">4</th>\r\n                                                        <th style=\"padding: 5px;\">5</th>\r\n                                                        <th style=\"padding: 5px;\">6</th>\r\n                                                        <th>Total Error/Average Feedback </th>\r\n                                                        <th>Rating </th>\r\n                                                        <th>Time Type</th>\r\n                                                        <th>Prescribed Period</th>\r\n                                                        <th style=\"padding: 5px;\">1</th>\r\n                                                        <th style=\"padding: 5px;\">2</th>\r\n                                                        <th style=\"padding: 5px;\">3</th>\r\n                                                        <th style=\"padding: 5px;\">4</th>\r\n                                                        <th style=\"padding: 5px;\">5</th>\r\n                                                        <th style=\"padding: 5px;\">6</th>\r\n                                                        <th style=\"padding: 5px;\">Total</th>\r\n                                                        <th>Ave. Time per Doc/Activity</th>\r\n                                                    </tr>\r\n                                                    <tr>\r\n                                                        <td style=\"padding: 5px;\">{{ dat.quantity_type }}</td>\r\n                                                        <td>{{ QuantityType(dat.quantity_type) }}</td>\r\n                                                        <td>{{ dat.quantity_sem }}</td>\r\n                                                        <td><span v-html=\"getScore(dat.result, 1, 7)\"></span></td>\r\n                                                        <td><span v-html=\"getScore(dat.result, 2, 8)\"></span></td>\r\n                                                        <td><span v-html=\"getScore(dat.result, 3, 9)\"></span></td>\r\n                                                        <td><span v-html=\"getScore(dat.result, 4, 10)\"></span></td>\r\n                                                        <td><span v-html=\"getScore(dat.result, 5, 11)\"></span></td>\r\n                                                        <td><span v-html=\"getScore(dat.result, 6, 12)\"></span></td>\r\n                                                        <td><span v-html=\"GetSumQuantity(dat.result)\"></span></td>\r\n                                                        <td>\r\n                                                            {{\r\n                dat.quantity_sem === \"0\"\r\n                    ? \"\"\r\n                    : (GetSumQuantity(dat.result) / dat.quantity_sem *\r\n                        100).toFixed(0) + \"%\"\r\n            }}\r\n                                                        </td>\r\n                                                        <td style=\"padding: 5px;\">{{ dat.quality_error }}</td>\r\n                                                        <td>{{ QualityType(dat.quality_error) }}</td>\r\n                                                        <td><span\r\n                                                                v-html=\"getQuality(dat.result, 1, 7, dat.quality_error)\"></span>\r\n                                                        </td>\r\n                                                        <td><span\r\n                                                                v-html=\"getQuality(dat.result, 2, 8, dat.quality_error)\"></span>\r\n                                                        </td>\r\n                                                        <td><span\r\n                                                                v-html=\"getQuality(dat.result, 3, 9, dat.quality_error)\"></span>\r\n                                                        </td>\r\n                                                        <td><span\r\n                                                                v-html=\"getQuality(dat.result, 4, 10, dat.quality_error)\"></span>\r\n                                                        </td>\r\n                                                        <td><span\r\n                                                                v-html=\"getQuality(dat.result, 5, 11, dat.quality_error)\"></span>\r\n                                                        </td>\r\n                                                        <td><span\r\n                                                                v-html=\"getQuality(dat.result, 6, 12, dat.quality_error)\"></span>\r\n                                                        </td>\r\n                                                        <td>{{ QualityTypes(dat.quality_error,\r\n                GetSumQuality(dat.result, dat.quality_error),\r\n                CountMonth(dat.result)) }}\r\n                                                        </td>\r\n                                                        <td>{{ dat.result.length == 0 ? 0 :\r\n                QualityRating(dat.quality_error,\r\n                    QualityTypes(dat.quality_error, GetSumQuality(dat.result,\r\n                        dat.quality_error),\r\n                                                            CountMonth(dat.result))) }}</td>\r\n                                                        <td>{{ dat.time_based }}</td>\r\n                                                        <td>{{ dat.time_range_code === 56 ? \"Not to be Rated\" :\r\n                                                            \"Prescribed Period is \" +\r\n                                                            // dat.indi_output.time_ranges.prescribed_period\r\n                                                            dat.prescribed_period\r\n                                                            + \" \" + dat.indi_output.unit_of_time }}\r\n                                                        </td>\r\n                                                        <td><span v-html=\"getTime(dat.result, 1, 7)\"></span>\r\n                                                        </td>\r\n                                                        <td><span v-html=\"getTime(dat.result, 2, 8)\"></span>\r\n                                                        </td>\r\n                                                        <td><span v-html=\"getTime(dat.result, 3, 9)\"></span>\r\n                                                        </td>\r\n                                                        <td><span v-html=\"getTime(dat.result, 4, 10)\"></span>\r\n                                                        </td>\r\n                                                        <td><span v-html=\"getTime(dat.result, 5, 11)\"></span>\r\n                                                        </td>\r\n                                                        <td><span v-html=\"getTime(dat.result, 6, 12)\"></span></td>\r\n                                                        <td><span v-html=\"TotalTime(dat.result)\"></span></td>\r\n                                                        <td><span\r\n                                                                v-html=\"AveTime(TotalTime(dat.result), GetSumQuantity(dat.result))\"></span>\r\n                                                        </td>\r\n                                                    </tr>\r\n                                                </tbody>\r\n                                            </table>\r\n                                            </p>\r\n                                        </Transition>\r\n                                    </td>\r\n                                </tr>\r\n                            </template>\r\n\r\n                            <tr>\r\n                                <td colspan=\"8\">\r\n                                    <b style=\"float:right\">Average Point Score - Support Function</b>\r\n                                </td>\r\n                                <td>\r\n                                    {{ Average_Point_Support }}\r\n                                </td>\r\n                            </tr>\r\n                            <tr>\r\n                                <td colspan=\"8\">\r\n                                    <b style=\"float:right\">Multiply by Weighted Allocation</b>\r\n                                </td>\r\n                                <td>\r\n                                    30%\r\n                                </td>\r\n                            </tr>\r\n                            <tr>\r\n                                <td colspan=\"8\">\r\n                                    <b style=\"float:right\">Weighted Average Score - Support Function</b>\r\n                                </td>\r\n                                <td>\r\n                                    {{ (Average_Point_Support * .30).toFixed(2) }}\r\n                                </td>\r\n                            </tr>\r\n                            <tr>\r\n                                <td colspan=\"8\">\r\n                                    <b style=\"float:right\">Total Average Score</b>\r\n                                </td>\r\n                                <td>\r\n                                    {{ getAdjectivalScore(Average_Point_Core * 0.70, Average_Point_Support * 0.30)}}\r\n                                </td>\r\n                            </tr>\r\n                            <tr>\r\n                                <td colspan=\"8\">\r\n                                    <b style=\"float:right\">Additional Point Intervening Factor - if applicable -\r\n                                        Maximum: 0.5 pts</b>\r\n                                </td>\r\n                                <td>\r\n\r\n                                </td>\r\n                            </tr>\r\n                            <tr>\r\n                                <td colspan=\"8\">\r\n                                    <b style=\"float:right\">Total Final Average Rating</b>\r\n                                </td>\r\n                                <td style=\"background-color: yellow\">\r\n                                    <b>{{\r\n                                        getAdjectivalScore(Average_Point_Core * 0.70, Average_Point_Support * 0.30)\r\n                                        }}</b>\r\n                                </td>\r\n                            </tr>\r\n                            <tr>\r\n                                <td colspan=\"8\">\r\n                                    <b style=\"float:right\">Final Adjectival Rating</b>\r\n                                </td>\r\n                                <td style=\"background-color: yellow\">\r\n                                    <b>{{ getAdjectivalRating(getAdjectivalScore(Average_Point_Core * 0.70,\r\n                                        Average_Point_Support * 0.30)) }}</b>\r\n                                </td>\r\n                            </tr>\r\n                            <tr>\r\n                                <td colspan=\"9\">\r\n                                    <b>Supervisor's comments and recommendations for development purposes or\r\n                                        Rewards/Promotion</b>\r\n                                </td>\r\n                            </tr>\r\n                            <tr>\r\n                                <td colspan=\"9\">\r\n                                    {{ sem_data.remarks }}<br>{{ sem_data.remarkshigher }}\r\n                                </td>\r\n                            </tr>\r\n                        </tbody>\r\n                    </table>\r\n                </div>\r\n\r\n                <!-- <div class=\"row justify-content-center\">\r\n                    <div class=\"col-md-12\">\r\n                        <pagination :next=\"data.next_page_url\" :prev=\"data.prev_page_url\" />\r\n                    </div>\r\n                </div> -->\r\n                <!-- <div class=\"row justify-content-center\">\r\n                    <div class=\"col-md-12\">\r\n                        <p>\r\n                            {{ data.from }} to {{ data.to }} of\r\n                            {{ data.total }} entries\r\n                        </p>\r\n                    </div>\r\n                </div> -->\r\n            </div>\r\n        </div>\r\n\r\n        <Modal v-if=\"displayModal\" @close-modal-event=\"hideModal\">\r\n            <div class=\"d-flex justify-content-center\">\r\n                <!-- {{ my_link }} -->\r\n                <iframe :src=\"my_link\" style=\"width:100%; height:450px\" />\r\n            </div>\r\n        </Modal>\r\n\r\n        <Modal v-if=\"displayModal1\" @close-modal-event=\"hideModal1\">\r\n            <div class=\"d-flex justify-content-center\">\r\n                <iframe :src=\"my_link\" style=\"width:100%; height:450px\" />\r\n            </div>\r\n        </Modal>\r\n\r\n        <Modals v-if=\"displayModal2\" @close-modal-event=\"hideModal2\">\r\n            <input type=\"text\" v-model=\"form.remarks\" class=\"form-control\" autocomplete=\"chrome-off\"><br>\r\n            <!-- <button class=\"btn btn-primary btn-sm mL-2 text-white\" @click=\"submit()\">Save Remarks</button> -->\r\n\r\n            <span v-if=\"form.remarks_id === ''\">\r\n                <button class=\"btn btn-primary btn-sm mL-2 text-white\" @click=\"submit()\">Add Remarks</button>\r\n            </span>\r\n            <span v-else>\r\n                <button class=\"btn btn-primary btn-sm mL-2 text-white\" @click=\"edit()\">Edit Remarks</button>\r\n                <button class=\"btn btn-primary btn-sm mL-2 text-white\"\r\n                    @click=\"deleteOutput(form.remarks_id, form.idSemestral)\">Delete Remarks</button>\r\n            </span>\r\n\r\n        </Modals>\r\n    </div>\r\n</template>\r\n<script>\r\n\r\nimport { useForm } from \"@inertiajs/inertia-vue3\";\r\nimport Filtering from \"@/Shared/Filter\";\r\nimport FilterPrinting from \"@/Shared/FilterPrint\";\r\nimport Pagination from \"@/Shared/Pagination\";\r\nimport Modal from \"@/Shared/PrintModal\";\r\nimport Modals from \"@/Shared/Modal\"\r\nexport default {\r\n    props: {\r\n        auth: Object,\r\n        emp_code: Object,\r\n        emp: Object,\r\n        sem_data: Object,\r\n        sem_id: String,\r\n        month: Object,\r\n        data: Object,\r\n        month_data: Object,\r\n        // sem: Object,\r\n        // dept: Object,\r\n        // pghead: Object,\r\n        division: Object,\r\n        dept_con: String,\r\n        pghead_con: String,\r\n        division_con: String,\r\n    },\r\n    data() {\r\n        return {\r\n            // search: this.$props.filters.search,\r\n            // filter: false,\r\n            filter_p: false,\r\n            displayModal: false,\r\n            displayModal1: false,\r\n            displayModal2: false,\r\n            my_link: \"\",\r\n            year: \"\",\r\n            opened: [],\r\n            // show: false,\r\n            show: [],\r\n            Average_Point_Core: 0,\r\n            Average_Point_Support: 0,\r\n            Average_Core: 0,\r\n            Average_Support: 0,\r\n            rating_data: {},\r\n            form: useForm({\r\n                remarks: \"\",\r\n                remarks_id: \"\",\r\n                year: \"\",\r\n                idIPCR: \"\",\r\n                idSemestral: \"\",\r\n                emp_code: \"\",\r\n            })\r\n            // mfosel: \"\",\r\n        }\r\n    },\r\n    watch: {\r\n        //     search: _.debounce(function (value) {\r\n        //     this.$inertia.get(\r\n        //         \"/AddAccomplishment\",\r\n        //         { search: value },\r\n        //         {\r\n        //             preserveScroll: true,\r\n        //             preserveState: true,\r\n        //             replace: true,\r\n        //         }\r\n        //     );\r\n        // }, 300),\r\n    },\r\n    components: {\r\n        Pagination, Filtering, Modal, FilterPrinting, Modals,\r\n    },\r\n    mounted() {\r\n        this.calculateAverageCore()\r\n        this.calculateAverageSupport()\r\n        this.setShow()\r\n    },\r\n    methods: {\r\n        getAdjectivalScore(Core, Support) {\r\n            var result = 0;\r\n            var result = Math.round((Core + Support) * 100) / 100;\r\n            return result;\r\n        },\r\n        getAdjectivalRating(Score) {\r\n            var result = \"\"\r\n            if (Score >= 4.51 && Score <= 5.00) {\r\n                result = \"Outstanding\"\r\n            } else if (Score >= 3.51 && Score <= 4.50) {\r\n                result = \"Very Satisfactory\"\r\n            } else if (Score >= 2.51 && Score <= 3.50) {\r\n                result = \"Satisfactory\"\r\n            } else if (Score >= 1.51 && Score <= 2.50) {\r\n                result = \"Unsatisfactory\"\r\n            } else if (Score >= 1.00 && Score <= 1.50) {\r\n                result = \"Poor\"\r\n            }\r\n\r\n            return result;\r\n        },\r\n        submit() {\r\n            var url = \"/semester-accomplishment/store\"\r\n            // alert('for store '+url);\r\n            this.form.post(url);\r\n\r\n            this.displayModal2 = false;\r\n\r\n            this.form.remarks = \"\";\r\n        },\r\n        edit() {\r\n            this.form.patch(\"/semester-accomplishment/\" + this.form.remarks_id, this.form);\r\n            this.form.remarks_id = \"\";\r\n            this.displayModal2 = false;\r\n        },\r\n        deleteOutput(id) {\r\n            this.$inertia.delete(\"/semester-accomplishment/\" + id);\r\n            this.form.remarks_id = \"\";\r\n            this.displayModal2 = false;\r\n        },\r\n        showFilter() {\r\n            //alert(\"show filter\");\r\n            this.filter = !this.filter\r\n        },\r\n        showFilterP() {\r\n            // alert(\"show filter\");\r\n            this.filter_p = !this.filter_p\r\n        },\r\n        AverageScore() {\r\n\r\n        },\r\n        showModal2(idIPCR, ipcr_semester, year) {\r\n            this.form.year = year;\r\n            // this.form.month = this.month;\r\n            this.form.emp_code = this.emp_code;\r\n            this.form.idIPCR = idIPCR;\r\n            this.form.idSemestral = ipcr_semester;\r\n            // alert(this.form.month);\r\n            this.displayModal2 = true;\r\n            this.form.remarks = \"\";\r\n            this.form.remarks_id = \"\";\r\n        },\r\n        showModal3(idIPCR, ipcr_semester, remarks, id) {\r\n            this.form.year = this.year;\r\n            this.form.emp_code = this.emp_code;\r\n            this.form.idIPCR = idIPCR;\r\n            this.form.idSemestral = ipcr_semester;\r\n            this.form.remarks = remarks;\r\n            this.form.remarks_id = id;\r\n\r\n            this.displayModal2 = true;\r\n        },\r\n        hideModal2() {\r\n            this.displayModal2 = false;\r\n        },\r\n        getScore(Item, month1, month2) {\r\n            var result = _.find(Item, obj => {\r\n                return obj.month == month1 || obj.month == month2;\r\n            });\r\n\r\n            return result ? result.quantity : ''\r\n        },\r\n        getQuality(Item, month1, month2, type) {\r\n            if (type == 1) {\r\n                var final = 0;\r\n                var result = _.find(Item, obj => {\r\n                    return obj.month == month1 || obj.month == month2;\r\n                });\r\n                if (result) {\r\n                    final = result.average_quality;\r\n                    if (final == 0) {\r\n                        final = 0;\r\n                    } else if (final >= 0.01 && final <= 1) {\r\n                        final = 1;\r\n                    } else if (result.average_quality >= 1.01 && result.average_quality <= 2) {\r\n                        final = 2;\r\n                    } else if (result.average_quality >= 2.01 && result.average_quality <= 3) {\r\n                        final = 3;\r\n                    } else if (result.average_quality >= 3.01 && result.average_quality <= 4) {\r\n                        final = 4;\r\n                    } else if (result.average_quality >= 4.01 && result.average_quality <= 5) {\r\n                        final = 5;\r\n                    } else if (result.average_quality >= 5.01 && result.average_quality <= 6) {\r\n                        final = 6;\r\n                    } else if (result.average_quality >= 6.01 && result.average_quality <= 7) {\r\n                        final = 7;\r\n                    }\r\n                    return final;\r\n                }\r\n                return result ? final : ''\r\n            } else if (type == 2) {\r\n                var result = _.find(Item, obj => {\r\n                    return obj.month == month1 || obj.month == month2;\r\n                });\r\n                return result ? Math.floor(result.average_quality, 0) : ''\r\n            } else if (type == 4){\r\n                var result = _.find(Item, obj => {\r\n                    return obj.month == month1 || obj.month == month2;\r\n                });\r\n\r\n                return result ? Math.floor(result.average_quality, 0) : ''\r\n            }\r\n        },\r\n        getTime(Item, month1, month2) {\r\n            var result = _.find(Item, obj => {\r\n                return obj.month == month1 || obj.month == month2;\r\n            });\r\n\r\n            return result ? result.average_time : ''\r\n        },\r\n        GetSumQuantity(Item) {\r\n            // _.sumBy(Item.slice,\r\n            var result = _.sumBy(Item.slice(0, 6), (o) => {\r\n                return Number(o.quantity)\r\n            });\r\n            return result;\r\n\r\n            // var result = _.sumBy(Item, (o) => {\r\n            //     // Convert the quantity to a number and check if it is a valid number\r\n            //     const quantity = Number(o.quantity);\r\n            //     return isNaN(quantity) ? 0 : quantity;\r\n            // });\r\n            // return result;\r\n            // var result = Item.reduce((sum, o) => {\r\n            //     const quantity = Number(o.quantity);\r\n            //     return sum + (isNaN(quantity) ? 0 : quantity);\r\n            // }, 0);\r\n            // return result;\r\n        },\r\n        GetSumQuality(Item, quality_error) {\r\n            if (quality_error == 1) {\r\n                var result = _.sumBy(Item, (o) => {\r\n                    return Number(o.average_quality)\r\n                });\r\n            } else if (quality_error == 2) {\r\n                var result = _.sumBy(Item, (o) => {\r\n                    return Number(o.average_quality)\r\n                });\r\n            } else if (quality_error == 3) {\r\n                var result = 0\r\n            } else if (quality_error == 4) {\r\n                var result = _.sumBy(Item, (o) => {\r\n                    return Number(o.average_quality)\r\n                });\r\n            }\r\n\r\n            return result;\r\n\r\n\r\n        },\r\n        CountMonth(Item) {\r\n            var result = Item.length\r\n            return result;\r\n        },\r\n        TotalTime(Item) {\r\n            var result = _.sumBy(Item, obj => {\r\n                return obj.average_time ? obj.average_time * obj.quantity : 0;\r\n            })\r\n            return result;\r\n        },\r\n        MonthlyAveTime(Time, TotalQuantity) {\r\n            var Time = Time\r\n            var TotalQuantity = TotalQuantity\r\n            var Result\r\n            if (Time == 0 && TotalQuantity == 0) {\r\n                Result = \"\"\r\n            } else {\r\n                Result = Math.round(Number(Time /\r\n                    TotalQuantity))\r\n            }\r\n            return Result;\r\n        },\r\n        AveTime(Time, TotalQuantity) {\r\n            var Time = Time\r\n            var TotalQuantity = TotalQuantity\r\n            var Result\r\n            if (Time == 0 && TotalQuantity == 0) {\r\n                Result = 0\r\n            } else {\r\n                Result = Math.round(Number(Time /\r\n                    TotalQuantity))\r\n            }\r\n            return Result;\r\n        },\r\n        QuantityRate(id, quantity, target) {\r\n            var result;\r\n            if (id == 1) {\r\n                var total = Math.round((quantity / target) * 100)\r\n                if (total >= 130) {\r\n                    result = \"5\"\r\n                } else if (total <= 129 && total >= 115) {\r\n                    result = \"4\"\r\n                } else if (total <= 114 && total >= 90) {\r\n                    result = \"3\"\r\n                } else if (total <= 89 && total >= 51) {\r\n                    result = \"2\"\r\n                } else if (total <= 50) {\r\n                    result = \"1\"\r\n                } else\r\n                    result = \"0\"\r\n            } else if (id == 2) {\r\n                if (quantity == target) {\r\n                    result = 5\r\n                } else {\r\n                    result = 2\r\n                }\r\n            }\r\n            return result;\r\n        },\r\n        QualityRate(id, total) {\r\n            var result;\r\n            if (id == 1) {\r\n                if (total == 0) {\r\n                    result = \"5\"\r\n                } else if (total >= .01 && total <= 2.99) {\r\n                    result = \"4\"\r\n                } else if (total >= 3 && total <= 4.99) {\r\n                    result = \"3\"\r\n                } else if (total >= 5 && total <= 6.99) {\r\n                    result = \"2\"\r\n                } else if (total >= 7) {\r\n                    result = \"1\"\r\n                } else {\r\n                    result = \"0\"\r\n                }\r\n            } else if (id == 2) {\r\n                if (total == 5) {\r\n                    result = \"5\"\r\n                } else if (total >= 4 && total <= 4.99) {\r\n                    result = \"4\"\r\n                } else if (total >= 3 && total <= 3.99) {\r\n                    result = \"3\"\r\n                } else if (total >= 2 && total <= 2.99) {\r\n                    result = \"2\"\r\n                } else if (total >= 1 && total <= 1.99) {\r\n                    result = \"1\"\r\n                } else {\r\n                    result = \"0\"\r\n                }\r\n            } else if (id == 3) {\r\n                result = \"0\"\r\n            } else if (id == 4) {\r\n                if (total >= 1) {\r\n                    result = \"2\"\r\n                } else {\r\n                    result = \"5\"\r\n                }\r\n            }\r\n            return result;\r\n        },\r\n        QuantityType(id) {\r\n            var result;\r\n            if (id == 1) {\r\n                result = \"TO BE RATED\"\r\n            } else {\r\n                result = \"ACCURACY RULE (100%=5,2 if less than 100%)\"\r\n            }\r\n            return result;\r\n        },\r\n        QualityType(id) {\r\n            var result;\r\n            if (id == 1) {\r\n                result = \"NO. OF ERROR\"\r\n            } else if (id == 2) {\r\n                result = \"AVE. FEEDBACK\"\r\n            } else if (id == 3) {\r\n                result = \"NOT TO BE RATED\"\r\n            } else if (id == 4) {\r\n                result = \"ACCURACY RULE\"\r\n            }\r\n            return result;\r\n        },\r\n        SemName(id) {\r\n            var result;\r\n            if (id == 1) {\r\n                result = \"January to June\"\r\n            } else {\r\n                result = \"July to December\"\r\n            }\r\n\r\n            return result;\r\n        },\r\n        QualityTypes(quality_type, score, length) {\r\n            var result;\r\n            if (quality_type == 1) {\r\n                result = score;\r\n                if (score == 0) {\r\n                    result = 0;\r\n                } else if (score >= 0.01 && score <= 1) {\r\n                    result = 1;\r\n                } else if (score >= 1.01 && score <= 2) {\r\n                    result = 2;\r\n                } else if (score >= 2.01 && score <= 3) {\r\n                    result = 3;\r\n                } else if (score >= 3.01 && score <= 4) {\r\n                    result = 4;\r\n                } else if (score >= 4.01 && score <= 5) {\r\n                    result = 5;\r\n                } else if (score >= 5.01 && score <= 6) {\r\n                    result = 6;\r\n                } else if (score >= 6.01 && score <= 7) {\r\n                    result = 7;\r\n                }\r\n                return result;\r\n            } else if (quality_type == 2) {\r\n                if (length == 0) {\r\n                    result = 0;\r\n                } else {\r\n                    result = Math.round(score / length);\r\n                }\r\n            } else if (quality_type == 3) {\r\n                result = score;\r\n            } else if (quality_type == 4) {\r\n                result = score;\r\n            }\r\n            return result;\r\n        },\r\n        QualityRating(quality_type, quality_score) {\r\n            var result;\r\n            if (quality_type == 1) {\r\n                if (quality_score == 0) {\r\n                    result = \"5\"\r\n                } else if (quality_score >= .01 && quality_score <= 2.99) {\r\n                    result = \"4\"\r\n                } else if (quality_score >= 3 && quality_score <= 4.99) {\r\n                    result = \"3\"\r\n                } else if (quality_score >= 5 && quality_score <= 6.99) {\r\n                    result = \"2\"\r\n                } else if (quality_score >= 7) {\r\n                    result = \"1\"\r\n                } else {\r\n                    result = \"0\"\r\n                }\r\n            } else if (quality_type == 2) {\r\n                if (quality_score == 5) {\r\n                    result = \"5\"\r\n                } else if (quality_score >= 4 && quality_score <= 4.99) {\r\n                    result = \"4\"\r\n                } else if (quality_score >= 3 && quality_score <= 3.99) {\r\n                    result = \"3\"\r\n                } else if (quality_score >= 2 && quality_score <= 2.99) {\r\n                    result = \"2\"\r\n                } else if (quality_score >= 1 && quality_score <= 1.99) {\r\n                    result = \"1\"\r\n                } else {\r\n                    result = \"0\"\r\n                }\r\n            } else if (quality_type == 3) {\r\n                result = \"0\"\r\n            } else if (quality_type == 4) {\r\n                if (quality_score >= 1) {\r\n                    result = \"2\"\r\n                } else {\r\n                    result = \"5\"\r\n                }\r\n            }\r\n\r\n            return result;\r\n        },\r\n        AverageRate(QuantityRating, QualityRating, TimeRating) {\r\n            // alert(TimeRating)\r\n\r\n            if (TimeRating == \" \") {\r\n                TimeRating = 0;\r\n            }\r\n            if (TimeRating == \"\") {\r\n                TimeRating = 0;\r\n            }\r\n            if (isNaN(TimeRating)) {\r\n                TimeRating = 0;\r\n            }\r\n            var ratings = [parseFloat(QuantityRating), parseFloat(QualityRating), parseFloat(TimeRating)];\r\n\r\n            var NotZero = ratings.filter(rating => rating !== 0);\r\n\r\n            if (NotZero.length === 0) {\r\n                return 0; // or any default value when all ratings are zero\r\n            }\r\n\r\n            const average = NotZero.reduce((sum, rating) => sum + rating, 0) / NotZero.length;\r\n\r\n\r\n            return this.format_number_conv(average, 2, true)\r\n\r\n\r\n        },\r\n        TimeRatings(Ave_Time, Range, Time_Code) {\r\n            // alert(Range);\r\n            var result;\r\n            var EQ;\r\n\r\n            if (Time_Code == 56) {\r\n                result = \" \";\r\n            } else {\r\n                try {\r\n\r\n                    Range.map(Item => {\r\n                        if (Ave_Time <= Item.equivalent_time_from && Item.rating == 5) {\r\n                            result = 5;\r\n                            EQ = Item.equivalent_time_from;\r\n                        } else if (Ave_Time >= Item.equivalent_time_from && Ave_Time <= Item.equivalent_time_to && Item.rating == 4) {\r\n                            result = 4;\r\n                            EQ = Item.equivalent_time_from;\r\n                        } else if (Ave_Time == Item.equivalent_time_from && Item.rating == 3) {\r\n                            result = 3;\r\n                            EQ = Item.equivalent_time_from;\r\n                        } else if (Ave_Time >= Item.equivalent_time_from && Ave_Time <= Item.equivalent_time_to && Item.rating == 2) {\r\n                            result = 2;\r\n                            EQ = Item.equivalent_time_from;\r\n                        } else if (Ave_Time >= Item.equivalent_time_from && Item.rating == 1) {\r\n                            result = 1;\r\n                            EQ = Item.equivalent_time_from;\r\n                        } else if (Ave_Time == 0) {\r\n                            result = 0;\r\n                        }\r\n                    })\r\n                } catch (error) {\r\n\r\n                }\r\n            }\r\n            return result;\r\n        },\r\n        calculateAverageCore() {\r\n            let sum = 0;\r\n            let num_of_data = 0;\r\n            let average = 0;\r\n\r\n            if (Array.isArray(this.data)) {\r\n                this.data.forEach(item => {\r\n                    if (item.ipcr_type === 'Core Function') {\r\n                        var val = this.AverageRate(item.result == 0 ? 0 : this.QuantityRate(item.quantity_type, this.GetSumQuantity(item.result),\r\n                            item.quantity_sem), item.result == 0 ? 0 : this.QualityRating(item.quality_error, this.QualityTypes(item.quality_error,\r\n                                this.GetSumQuality(item.result, item.quality_error), this.CountMonth(item.result))),\r\n                            this.TimeRatings(this.AveTime(this.TotalTime(item.result), this.GetSumQuantity(item.result)), item.indi_output.time_ranges, item.time_range_code));\r\n                        // alert(val);\r\n                        // alert(this.TimeRatings(this.AveTime(this.TotalTime(item.result), this.GetSumQuantity(item.result)), item.TimeRange, item.time_range_code));\r\n                        if (val !== 0) {\r\n                            num_of_data += 1;\r\n                            sum += parseFloat(val);\r\n                            average = sum / num_of_data\r\n                        }\r\n\r\n                    }\r\n                    // console.log(num_of_data);\r\n                    // console.log(average)\r\n                });\r\n            }\r\n            this.Average_Point_Core = average.toFixed(2);\r\n        },\r\n        calculateAverageSupport() {\r\n            let sum = 0;\r\n            let num_of_data = 0;\r\n            let average = 0;\r\n            if (Array.isArray(this.data)) {\r\n                this.data.forEach(item => {\r\n                    if (item.ipcr_type === 'Support Function') {\r\n                        var val = this.AverageRate(item.result == 0 ? 0 : this.QuantityRate(item.quantity_type, this.GetSumQuantity(item.result),\r\n                            item.quantity_sem), item.result == 0 ? 0 : this.QualityRating(item.quality_error, this.QualityTypes(item.quality_error,\r\n                                this.GetSumQuality(item.result, item.quality_error), this.CountMonth(item.result))),\r\n                            this.TimeRatings(this.AveTime(this.TotalTime(item.result), this.GetSumQuantity(item.result)), item.indi_output.time_ranges, item.time_range_code));\r\n                        // alert(val);\r\n\r\n                        if (val !== 0) {\r\n                            num_of_data += 1;\r\n                            sum += parseFloat(val);\r\n                            average = sum / num_of_data\r\n                        }\r\n                    }\r\n                });\r\n            }\r\n\r\n            this.Average_Point_Support = average.toFixed(2);\r\n        },\r\n\r\n        showCreate() {\r\n            this.$inertia.get(\r\n                \"/targets/create\",\r\n                {\r\n                    raao_id: this.raao_id\r\n                },\r\n                {\r\n                    preserveScroll: true,\r\n                    preserveState: true,\r\n                    replace: true,\r\n                }\r\n            )\r\n        },\r\n        getAccomplishment(tar_id) {\r\n            this.$inertia.get(\r\n                \"/accomplishments\",\r\n                {\r\n                    idtarget: tar_id\r\n                },\r\n                {\r\n                    preserveScroll: true,\r\n                    preserveState: true,\r\n                    replace: true,\r\n                }\r\n            );\r\n        },\r\n        getPercent(accomp, targqty) {\r\n            var accSum = 0;\r\n            accomp.forEach(myFunction);\r\n            function myFunction(item) {\r\n                accSum += parseFloat(item.accomplishment_qty)\r\n            }\r\n            var percentt = (accSum / targqty) * 100\r\n            percentt = this.format_number(percentt, 2, true)\r\n            return percentt;\r\n        },\r\n        printSubmit1() {\r\n            // alert(this.Average_Point_Core);\r\n            //var office_ind = document.getElementById(\"selectOffice\").selectedIndex;\r\n            // this.office =this.auth.user.office.office;\r\n            // var pg_head = this.functions.DEPTHEAD;\r\n            // var forFFUNCCOD = this.auth.user.office.department_code;\r\n            // alert(this.)\r\n\r\n\r\n            // console.log(this.emp.office.pg_head.first_name + \" \" + this.emp.office.pg_head.middle_name[0] + \". \" + this.emp.office.pg_head.last_name)\r\n            // var pg_heads_postfix = \"\";\r\n            // var pg_heads_suffix = \"\";\r\n            var pg_heads = this.pghead_con;\r\n            // if (this.emp.office) {\r\n            //     if (this.emp.office.pg_head) {\r\n            //         if (this.emp.office.pg_head.postfix_name != \"\") {\r\n            //             pg_heads_postfix = \", \" + this.emp.office.pg_head.postfix_name;\r\n            //         }\r\n            //         if (this.emp.office.pg_head.suffix_name != \"\") {\r\n            //             pg_heads_suffix = \", \" + this.emp.office.pg_head.suffix_name;\r\n            //         }\r\n            //         pg_heads = this.emp.office.pg_head.first_name + \" \" + this.emp.office.pg_head.middle_name[0] + \". \" + this.emp.office.pg_head.last_name + pg_heads_suffix + pg_heads_postfix;\r\n\r\n            //     }\r\n\r\n\r\n            // }\r\n\r\n            // console.log(this.division)\r\n            var suffix_imm = \"\";\r\n            var suffix_next = \"\";\r\n            var suffix_a = \"\";\r\n\r\n            if (this.sem_data.imm.suffix_name != \"\") {\r\n                suffix_imm = ', ' + this.sem_data.imm.suffix_name;\r\n            }\r\n            if (this.sem_data.next.suffix_name != \"\") {\r\n                suffix_next = ', ' + this.sem_data.next.suffix_name;\r\n            }\r\n            if (this.auth.user.name.suffix_name != \"\") {\r\n                suffix_a = ', ' + this.auth.user.name.suffix_name;\r\n            }\r\n\r\n            var post_imm = \"\";\r\n            var post_next = \"\";\r\n            var post_a = \"\";\r\n            if (this.sem_data.imm.postfix_name != \"\") {\r\n                post_imm = \", \" + this.sem_data.imm.postfix_name;\r\n            }\r\n            if (this.sem_data.next.postfix_name != \"\") {\r\n                post_next = \", \" + this.sem_data.next.postfix_name;\r\n            }\r\n            if (this.auth.user.name.postfix_name != \"\") {\r\n                post_a = ', ' + this.auth.user.name.postfix_name;\r\n            }\r\n\r\n            //MIDDLE NAME\r\n            var mid_imm = \"\";\r\n            var mid_next = \"\";\r\n            var mid_a = \"\";\r\n            if (this.sem_data.imm.middle_name != \"\") {\r\n                mid_imm = this.sem_data.imm.middle_name[0] + \". \";\r\n            }\r\n            if (this.sem_data.next.middle_name != \"\") {\r\n                mid_next = this.sem_data.next.middle_name[0] + \". \";\r\n            }\r\n            if (this.auth.user.name.middle_name != \"\") {\r\n                mid_a = this.auth.user.name.middle_name[0] + \". \";\r\n            }\r\n            //dept_con =>emp.office.office\r\n            this.my_link = this.viewlink1(this.sem_data.employee_code, this.auth.user.name.first_name + \" \" + mid_a +\r\n                this.auth.user.name.last_name + suffix_a + post_a, this.auth.user.name.employment_type_descr,\r\n                this.auth.user.name.position_long_title, this.dept_con, this.sem_data.division,\r\n                this.sem_data.imm.first_name + \" \" + mid_imm + this.sem_data.imm.last_name + suffix_imm + post_imm,\r\n                this.sem_data.next.first_name + \" \" + mid_next + this.sem_data.next.last_name + suffix_next + post_next,\r\n                this.sem_data.sem, this.sem_data.year, this.sem_data.id,\r\n                this.getPeriod(this.sem_data.sem, this.sem_data.year),\r\n                pg_heads, '3.33', '4.55');\r\n            console.log(this.sem_data.division);\r\n            // this.Average_Point_Core, this.Average_Point_Support\r\n            this.showModal1();\r\n            // console.log(this.my_link);\r\n            // console.log(this.division)\r\n        },\r\n        viewlink1(emp_code, employee_name, emp_status, position, office, division, immediate, next_higher, sem, year, idsemestral, period, pghead, Average_Score) {\r\n\r\n\r\n            //var linkt =\"abcdefghijklo534gdmoivndfigudfhgdyfugdhfugidhfuigdhfiugmccxcxcxzczczxczxczxcxzc5fghjkliuhghghghaaa555l&&&&-\";\r\n            var linkt = \"http://\";\r\n            var jasper_ip = this.jasper_ip;\r\n            var jasper_link = 'jasperserver/flow.html?pp=u%3DJamshasadid%7Cr%3DManager%7Co%3DEMEA%2CSales%7Cpa1%3DSweden&_flowId=viewReportFlow&_flowId=viewReportFlow&ParentFolderUri=%2Freports%2FIPCR%2FIPCR_Semester&reportUnit=%2Freports%2FIPCR%2FIPCR_Semester%2FSemester_Accomplishment_part1&standAlone=true&decorate=no&output=pdf';\r\n            var params = '&emp_code=' + emp_code + '&employee_name=' + employee_name +\r\n                '&emp_status=' + emp_status + '&position=' + position +\r\n                '&office=' + office + '&division=' + division + '&immediate=' + immediate +\r\n                '&next_higher=' + next_higher + '&sem=' + sem + '&year=' + year +\r\n                '&idsemestral=' + idsemestral 