te=no&output=pdf';\r\n            var params = '&emp_code=' + emp_code + '&employee_name=' + employee_name + '&emp_status=' + emp_status + '&position=' + position + '&office=' + office + '&division=' + division + '&immediate=' + immediate + '&next_higher=' + next_higher + '&sem=' + sem + '&year=' + year + '&idsemestral=' + idsemestral + '&period=' + period;\r\n            var linkl = linkt + jasper_ip + jasper_link + params;\r\n            // console.log(params)\r\n            return linkl;\r\n        },\r\n        showModal() {\r\n            this.displayModal = true;\r\n        },\r\n        hideModal() {\r\n            this.displayModal = false;\r\n        },\r\n        setShow() {\r\n            for (var x = 0; x < this.data.length; x++) {\r\n                this.show.push(false);\r\n            }\r\n        },\r\n        toggle(Item, i) {\r\n\r\n\r\n            axios.post('/semester-accomplishment/get-time-ranges', { time_range_code: Item.time_range_code })\r\n                .then(response => {\r\n                    this.rating_data = response.data\r\n\r\n                    const index = this.opened.indexOf(Item.ipcr_code);\r\n                    if (index > -1) {\r\n                        // this.opened.splice(index, 1)\r\n                    } else {\r\n                        this.opened = [];\r\n                        this.opened.push(Item.ipcr_code)\r\n                    }\r\n                    // alert(this.show);\r\n                    setTimeout(() => {\r\n                        // alert(this.show);\r\n                        // this.show = !this.show;\r\n                        for (var t = 0; t < this.data.length; t++) {\r\n                            if (i != t) {\r\n                                this.show[t] = false\r\n                            }\r\n\r\n                        }\r\n                        this.show[i] = !this.show[i];\r\n                    }, 100);\r\n                })\r\n\r\n        },\r\n        async filterData() {\r\n            //alert(this.mfosel);\r\n\r\n            this.$inertia.get(\r\n                \"/AddAccomplishment\",\r\n                {\r\n                    mfosel: this.mfosel\r\n                },\r\n                {\r\n                    preserveScroll: true,\r\n                    preserveState: true,\r\n                    replace: true,\r\n                }\r\n            );\r\n        },\r\n        clearFilter() {\r\n\r\n            this.mfosel = \"\";\r\n            this.search = \"\";\r\n            this.filterData();\r\n        },\r\n        submitAccomplishmentFOrThisMonth() {\r\n            // alert(\"submitAccomplishmentFOrThisMonth\");\r\n            let text = \"Are you sure you want to submit this accomplishment?\" + this.sem_id;\r\n            // alert(text);\r\n            if (confirm(text) == true) {\r\n                this.$inertia.post('/semester-accomplishment/submit/ipcr/semestral/' + this.sem_id);\r\n            } else {\r\n                alert('undo')\r\n            }\r\n            //\r\n        },\r\n        recallAccomplishmentFOrThisMonth() {\r\n            let text = \"Are you sure you want to submit this accomplishment?\" + this.sem_id;\r\n            // alert(text);\r\n            if (confirm(text) == true) {\r\n                this.$inertia.post('/semester-accomplishment/submit/ipcr/semestral/recall/' + this.sem_id);\r\n            } else {\r\n                alert('undo')\r\n            }\r\n        }\r\n    }\r\n};\r\n</script>\r\n<style>\r\n/***TABLE FULL WIDTH */\r\n.full-width {\r\n    width: 100%;\r\n}\r\n\r\n/**ACCORDION BEGIN*********************/\r\n.my-table {\r\n    width: 100%;\r\n    border: 1px solid #ccc;\r\n}\r\n\r\n.my-td {\r\n    padding: 2px;\r\n    border: 1px solid #ccc;\r\n}\r\n\r\n.opened {\r\n    background-color: rgb(2, 255, 251);\r\n}\r\n\r\n\r\n/**ACCORDION END*********************/\r\n.row-centered {\r\n    text-align: center;\r\n}\r\n\r\n.col-centered {\r\n    display: inline-block;\r\n    float: 